<template>
    <div class="complaintReporting" :class="{complaintReportingThemeL:bindThemeL}" title="工单列表">
        <!-- 头部 -->
        <navigation-bar  @onLeftClick="onLeftClick" :pageName="title" :bothWidth="headerItemWidth" >
            <template v-slot:nav-right>
               <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onRightClick">{{rightName}}</div>
            </template>
       </navigation-bar>

       <!-- 切换列表  -->
       <van-tabs v-model="active" swipeable animated @click="tabClick" @change="tabChange">
            <van-tab  title="全部" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <van-pull-refresh v-model="isLoading" @refresh="onRefresh" v-if="active == 0">
                    <van-list
                        v-model="loading"
                        :finished="finished"
                        @load="onLoad"
                        v-if="list.length > 0"
                        :offset="30"
                    >
                    <div class="topSpace"></div>
                    <complaint-list :list='list' @refresh="onRefresh" :flag="typeFlag"  v-if="list.length > 0"></complaint-list>
                    <div class="topSpace"></div>
                    </van-list>
                        <div class="noData" v-if="list.length == 0 && pageFlag == 0">
                            <div class="lineSpace"></div>
                            <img :src="noDataImg" class="noDataImg">
                            <div class="noDataTxt">暂无信息</div>
                        </div>
                        <div class="netWork" v-if="pageFlag == 1">
                            <div class="lineSpace"></div>
                            <network-error></network-error>
                        </div>
                        
                        
                </van-pull-refresh>
            </van-tab>

            <van-tab  title="处理中" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <van-pull-refresh v-model="isLoading" @refresh="onRefresh" v-if="active == 1">
                        <van-list
                            v-model="loading"
                            :finished="finished"
                            @load="onLoad"
                            v-if="list.length > 0"
                            :offset="30"
                            >
                            <div class="topSpace"></div>
                            <complaint-list :list='list' @refresh="onRefresh" :flag="typeFlag" v-if="list.length > 0"></complaint-list>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noData" v-if="list.length == 0 && pageFlag == 0">
                            <div class="lineSpace"></div>
                            <img :src="noDataImg" class="noDataImg">
                            <div class="noDataTxt">暂无信息</div>
                        </div>
                         <div class="netWork" v-if="pageFlag == 1">
                            <div class="lineSpace"></div>
                            <network-error></network-error>
                        </div>
                </van-pull-refresh>
            </van-tab>

            <van-tab  title="待评价" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <van-pull-refresh v-model="isLoading" @refresh="onRefresh" v-if="active == 2">
                    <van-list
                        v-model="loading"
                        :finished="finished"
                        @load="onLoad"
                        :offset="30"
                        v-if="list.length > 0"
                        >
                        <div class="topSpace"></div>
                        <complaint-list :list='list' @refresh="onRefresh" :flag="typeFlag" v-if="list.length > 0"></complaint-list>
                        <div class="topSpace"></div>
                    </van-list>

                    <div class="noData" v-if="list.length == 0 && pageFlag == 0">
                        <div class="lineSpace"></div>
                        <img :src="noDataImg" class="noDataImg">
                        <div class="noDataTxt">暂无信息</div>
                    </div>
                     <div class="netWork" v-if="pageFlag == 1">
                            <div class="lineSpace"></div>
                            <network-error></network-error>
                        </div>
                    
                </van-pull-refresh>
            </van-tab>

            <van-tab  title="已完成" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
            <van-pull-refresh v-model="isLoading" @refresh="onRefresh" v-if="active == 3">
                <van-list
                    v-model="loading"
                    :finished="finished"
                    @load="onLoad"
                    :offset="30" 
                    v-if="list.length > 0"
                >
                    <div class="topSpace"></div>
                   <complaint-list :list='list' @refresh="onRefresh" :flag="typeFlag" v-if="list.length > 0"></complaint-list>
                    <div class="topSpace"></div>
                </van-list>
                <div class="noData" v-if="list.length ==0 && pageFlag == 0">
                    <div class="lineSpace"></div>
                    <img :src="noDataImg" class="noDataImg">
                    <div class="noDataTxt">暂无信息</div>
                </div>
                 <div class="netWork" v-if="pageFlag == 1">
                            <div class="lineSpace"></div>
                            <network-error></network-error>
                </div>
            </van-pull-refresh>
        </van-tab>
        </van-tabs>
    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import RightImg from '@/common/servers/complaintReport/RightImg.vue';
import ComplaintList from '@/page/Services/ComplaintReporting/ComplaintList'
import { Dialog,Toast } from 'vant';
import {getWorkOrderList,workOrderCancel,remindWorkOrder} from '@/services/servers/compaintReporting'
import NetworkError from '@/common/networkError/NetworkError'
export default {
    name:"complaintReporting",
    components:{
        NavigationBar,
        RightImg,
        ComplaintList,
        NetworkError
    },
    data(){
        return{
            status:0,
            noData:0,
            userInfo:{
                'ownerPhone':'15725233749',
            },
            pageFlag:3,
            firstActive:0,
            isClick:0,
            title:'投诉建议',
            rightName:'我要投诉',
            active:0,
            noDataImg:require('@/assets/images/default/img_default@2x.png'),
            isLoading: false,
            nowPage:1,
            list: [],
            num:0,
            loading: false,
            finished: false,
            pageTotal:0,
            tabList:['全部','受理中','未评价','已完成'],
            tabLists:[
                {'title':'全部'},
                {'title':'受理中'},
                {'title':'未评价'},
                {'title':'已完成'},
            ],
            headerItemWidth:{
                width:'110px'
            },
         
            iconImg:require('@/assets/images/home/property_order/icon_list_house.png'),

            /*大字模式*/
            bindThemeL: this.$curTheme==0?false:true,
            titleThemeL: this.$curTheme==0?"14px":"17px"
        }
    },
    mounted(){
        if(this.typeFlag == 0){
            this.title = '报事报修'
            this.rightName = '我要报修'
        }else{
            this.title = "投诉建议"
             this.rightName = '我要投诉'
        }
    },
    activated(){
    
    },
    deactivated(){
        
    },
    watch:{
        active:{
            handler(newName, oldName) {
                this.nowPage = 1;
                this.list = [];
                this.loading = true;
                this.finished = false;
                if(this.loading) {
                    this.init();
                }  
              
    　　    },
    　　    immediate: true
        },
       
        
    },
    computed:{
        typeFlag(){
            return this.$store.getters.getTypeFlag
        },
      
    },
    methods:{
        // 加载列表数据
        init(){
            
            let params = {
                'ownerCode':this.$userInfo.OWNER_CODE,
                'page':this.nowPage,
                'rows':'3',
                'ownerPhone':this.$userInfo.PHONE,
                'typeFlag':this.typeFlag,
            };
            this.pageFlag = 3;
            if(this.active > 0){
                params.ownerStatus = this.active;
            };
            let that = this;
            getWorkOrderList(params).then(
                res => {
                     this.pageFlag = 0
                    let con = res.data.list
                    if(con.length > 0){
                        this.list.push(...con)
                    }
                    this.loading = false;
                    that.pageTotal = res.data.total;
                    if(this.list.length >= res.data.total){
                        this.finished = true;
                    }
                    this.isLoading = false
                    this.nowPage++;
                
                    
                },
                err => {
                  this.pageFlag = 1
                   this.isLoading = false;
                   this.loading = false;
                  this.finished = true
                  
                }
            )
        },
      
        tabChange(name,title){
            
        },
        tabClick(){
       
        },
        onLeftClick(){
           this.$router.go(-1);
        },
        onRightClick(){
           this.$router.push({
                name: 'complaintReportingAdd',
                query: {
                  typeFlag:this.typeFlag,
                }
            })
        },
        onRefresh() {
            setTimeout(() => {
                this.nowPage = 1;
                this.list = [];
                this.loading = true;
                this.finished = false;
                this.init()
            }, 500);
        },
        onLoad() {
         
            this.init();
        },
  
    }
}
</script>

<style >
.complaintReporting{
    width: 100%;
    height: 100%;
    
    background-color: #f9f9f9;
    display: flex;
    flex-direction: column;
}
.complaintReporting .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
}
.complaintReporting .rightBtnThemeL{
  font-size: 17px;
}
.complaintReporting .tabBox{
    width: 100%;
    height: 100%;
}

.complaintReporting .van-tabs__content{
    
    height: 100%;
}
.complaintReporting .van-tabs__wrap{

    background-color: #f9f9f9;
    z-index: 200;
}
/* 列表 */
.complaintReporting .list{
    width: 100%;
}
.complaintReporting .topSpace{
    width: 100%;
    height: 15px;
  
}
.complaintReporting .spaceLine{
    width: 100%;
    height: 8px;
}
.complaintReporting .van-cell{
    padding: 0;
    border: 0;
    outline: none;
    box-shadow: none;
    background-color: #f9f9f9;
}
.complaintReporting .van-cell:not(:last-child)::after{
    border: none
}
.complaintReporting .item{
    /*width: 100%;*/
    width: 92%;
    margin: 8px 4%;
    height: 209px;
    background: #FFFFFF;
    box-shadow: 0 0 6px 0 rgba(219,219,219,0.50);
    border-radius: 3px;
    position: relative;
    
}
.complaintReporting .item1{
    margin-top: 16px;
}
.complaintReporting .complaintBox{
    width: calc(100% - 40px);
    height:calc(100% - 20px);
    /* display: flex;
    flex-wrap: wrap; */
    padding: 20px 20px 0 20px;
}
.complaintReporting .workHeader{
    height: 44px;
    width: 100%;
    display: flex;
}
.complaintReporting .iconImg{
    display: block;
    width: 26px;
    height: 26px;
}
.complaintReporting .headerCon{
    flex: 1;
    /*margin-top: 3px;*/
    margin-top: -5px;
    padding-left: 5px;
}
.complaintReporting .workTitle{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.04px;
    line-height: 20px;
    height: 20px;
}
.complaintReporting .workTime{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #9A9A9A;
    letter-spacing: 0.16px;
    line-height: 15px;
}
.complaintReporting .workLabel{
    width: 100%;
    line-height: 25px;
    height: 25px;
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #333333;
    letter-spacing: -0.05px;
}

.complaintReporting .spaceLine{
    width: 100%;
    height: 2px;
    background-color: #E5E5E5;
    opacity: .4;
    margin-top: 18px;
}
.complaintReporting .btnList{
   
   
}

.complaintReporting .redBtn{
    border: 1px solid #17B7BD;
    color: #17B7BD;
}
.complaintReporting .van-pull-refresh__track{
    height: 100%;
}
.complaintReporting .workStatus{
    position: absolute;
    top: 0;
    right: 0;
    width: 66px;
    height: 32px;
    line-height: 32px;
   
}
.complaintReporting .noData{
    width: 100%;
    height:calc(100% - 100px);
    
}
.complaintReporting .noDataImg{
    width: 126px;
    display: block;
    margin:0 auto
}
.complaintReporting .lineSpace{
    width: 100%;
    height: 110px;
}
.complaintReporting .noDataTxt{
    width: 100%;
    text-align: center;
    font-size: 14px;
    line-height: 42px;
    color:#9A9A9A;
}
.complaintReportingThemeL .noDataTxt{
  font-size: 15px;
}
.complaintReporting .van-pull-refresh {
  height: 100%;
  overflow-y: auto
}
.complaintReporting .van-tab__pane{
  height: 100%;
  
}
.complaintReporting .van-tab__pane-wrapper{
  height: 100%;
}
.complaintReporting .van-tabs{
    flex: 1;
    overflow: hidden;
}
.complaintReporting .van-list{
    height: 100%;
    background-color: #fff;
    
}
</style>