<template>
    <div class="complaintReportingCard" v-if="list.length > 0">
        <div class="complaintReportingList" >
             <div class="complCard" v-for="(item,index) in list" :key="index">
                <div :class="{item:true,item1:index == 0,itemThemeL:bindThemeL}">
                <div class="complaintBox">
                    <div class="workHeader" @click="toDetail(item.complCode)">
                        <div class="headerIcon">
                                <img :src="iconImg" class="iconImg" :class="{iconImgThemeL:bindThemeL}">
                        </div>
                        <div class="headerCon" :class="{headerConThemeL:bindThemeL}">
                            <div class="workTitle" :class="{workTitleThemeL:bindThemeL}">{{item.areaName}}{{item.buildName}}{{item.roomName}}</div>
                            <div class="workTime" :class="{workTimeThemeL:bindThemeL}">{{item.createdTime}}</div>
                        </div>
                    </div>
                    <div class="workLabel" :class="{workLabelThemeL:bindThemeL}" @click="toDetail(item.complCode)">#{{item.labelName}}#</div>
                        <div class="workCon" :class="{workConThemeL:bindThemeL}" @click="toDetail(item.complCode)">{{item.content}}</div>
                        <div class="spaceLine"></div>
                        <div class="btnList">
                            <div :class="{btnItem:true,redBtn:true,btnItemThemeL:bindThemeL}" v-if="item.status == 0" @click="updateWorkOrder(item)">修改</div>
                            <div :class="{btnItem:true,btnItemThemeL:bindThemeL}" @click="cancel(item)" v-if="item.status == 0">撤回</div>
                                                <!-- 受理中 -->
                            <div class="btnItem redBtn" :class="{btnItemThemeL:bindThemeL}" v-if="item.status==1" @click="remindWorkOrder1(item)">催单</div>
                            <div class="btnItem" :class="{btnItemThemeL:bindThemeL}" v-if="item.status == 3" @click="toDetail(item.complCode)">查看</div>
                            <div class="btnItem redBtn" :class="{btnItemThemeL:bindThemeL}" v-if="item.status == 2" @click="evaluate(item)">评价</div>
                        </div>
                    <div class="workStatus" @click="toDetail(item.complCode)">
                        <right-img :labelType="item.status" :isClose="item.closeType"></right-img>
                        </div>
                        </div>
                    </div>
            </div>
           <div class="bottomSpace">

           </div>
        </div>
       
    </div>
</template>

<script>
import RightImg from '@/common/servers/complaintReport/RightImg.vue';
import { Dialog,Toast } from 'vant';
import {getWorkOrderList,workOrderCancel,remindWorkOrder} from '@/services/servers/compaintReporting'
export default {
    name:'complaintReporting',
    props:{
        list:{
            type:Array,
            default:[]
        },
        flag:{
            type:Number,
            default:0
        }
    },
     components:{
        RightImg, 
    },
    data(){
        return{
            iconImg:require('@/assets/images/home/property_order/icon_list_house.png'),
            /*大字模式*/
            bindThemeL: this.$curTheme==0 ? false : true
        }
    },
    methods:{
        cancel(item){
            let con = {
                'complCode':item.complCode,
                "updatedBy":this.$userInfo.OWNER_CODE,
            };
            Dialog.confirm({
                title: '确定要撤回报修订单？'
            }).then(() => {
                // on confirm
                   workOrderCancel(con).then(
                    res =>{
                        console.log(res)
                        if(res.resultCode == '0'){
                            Toast('撤销成功');
                            this.onload();
                        }else{
                            console.log('/....')
                            Toast(res.msg)
                        }
                    },
                    req => {
                        
                    }
                )
            }).catch(() => {
                // on cancel
            });
        },

        // 列表跳转详情
        toDetail(complateCode){
            this.$router.push({
                name: 'complaintReportingDetail',
                query: {
                  complateCode:complateCode,
                  typeFlag:this.flag,
                }
            })
        },

        // 修改工单
        updateWorkOrder(item){
            this.$router.push({
                name: 'complaintReportingAdd',
                query: {
                  complateCode:item.complCode,
                  typeFlag:this.flag,
                }
            })
        },

        // 催单
        remindWorkOrder1(item){
            let data = {
                "complCode": item.complCode,
                "remindOwnerCode": this.$userInfo.OWNER_CODE,
                "remindOwnerName": this.$userInfo.OWNER_NAME,
                "remindOwnerPhone": this.$userInfo.PHONE,
            };

            remindWorkOrder(data).then(
                res => {
                    if (res.resultCode == 0) {
                        Toast('成功为您催单');

                    } else {
                       Toast(res.msg)
                    }
                },
                err => {
                   
                }
            )
        },

        // 评价
        evaluate(item){
            this.$router.push({
                name: 'complaintReportingEvaluate',
                query: {
                  complateCode:item.complCode,
                }
            })
        },
        
        // 重新加载数据
        onload(){
            this.$emit('refresh')
        }
        // 

    }
   
}
</script>

<style >
.complaintReportingCard{
    width: 100%;
    height: 100%;
    background-color: #fff;
}
.complaintReportingCard .complaintReportingList{
    height: 100%;
}
.complaintReportingCard .list{
    width: 100%;

}
.complaintReportingCard .spaceLine{
    width: 100%;
    height: 8px;
}
.complaintReportingCard .van-cell{
    padding: 0;
    border: 0;
    outline: none;
    box-shadow: none;
    background-color: #f9f9f9;
}
.complaintReportingCard .van-cell:not(:last-child)::after{
    border: none
}
.complaintReportingCard .item{
    width: 100%;
    width: 92%;
    margin: 8px 4%;
    height: 209px;
    background: #FFFFFF;
    box-shadow: 0 0 6px 0 rgba(219,219,219,0.50);
    border-radius: 3px;
    position: relative;
}
.complaintReportingCard .itemThemeL {
    height: 215px !important;
}

.complaintReportingCard .complaintBox{
    width: calc(100% - 40px);
    height:calc(100% - 20px);
    /* display: flex;
    flex-wrap: wrap; */
    padding: 20px 20px 0 20px;
}
.complaintReportingCard .workHeader{
    height: 44px;
    width: 100%;
    display: flex;
}
.complaintReportingCard .iconImg{
    display: block;
    width: 26px;
    height: 26px;
}
.complaintReportingCard .iconImgThemeL{
    padding-top: 5px;
}
.complaintReportingCard .headerCon{
    flex: 1;
    margin-top: 3px;
    padding-left: 5px;
    width:calc(100% - 5px)
}
.complaintReportingCard .headerConThemeL {
    margin-top: -2px !important;
}
.complaintReportingCard .workTitle{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #4A4A4A !important;
    letter-spacing: -0.04px;
    line-height: 20px;
    height: 20px;
    overflow:hidden;
    width: 75%;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.complaintReportingCard .workTitleThemeL{
    font-size: 17px !important;
    line-height: 24px !important;
    height: 24px !important;
}
.complaintReportingCard .workTime{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #9A9A9A;
    letter-spacing: 0.16px;
    line-height: 15px;
}
.complaintReportingCard .workTimeThemeL{
    font-size: 14px !important;
    height: 18px !important;
    line-height: 18px !important;
}
.complaintReportingCard .workLabel{
    width: 100%;
    line-height: 25px;
    height: 25px;
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #333333;
    letter-spacing: -0.05px;
}
.complaintReportingCard .workLabelThemeL{
    font-size: 20px !important;
    line-height: 28px !important;
    height: 28px !important;
    margin-top: 6px !important;
}
.complaintReportingCard .workCon{
    height: 40px;
    width: 100%;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #4A4A4A !important;
    letter-spacing: -0.04px;
    margin-top: 6px;
    line-height: 20px;
    max-height: 40px;
    display: -webkit-box;
    word-wrap:break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
.complaintReportingCard .workConThemeL{
    font-size: 15px !important;
}
.complaintReportingCard .spaceLine{
    width: 100%;
    height: 2px;
    background-color: #E5E5E5;
    opacity: .4;
    margin-top: 18px;
}
.complaintReportingCard .btnList{
    width: 100%;
    height: 53px;
    display: flex;
    flex-direction:row-reverse;
    align-items:center;
}
.complaintReportingCard .btnItem{
    background: #FFFFFF;
    border: 1px solid #666666;
    border-radius: 20px;
    width: 92px;
    height: 30px;
    display:flex;
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #666666;
    letter-spacing: -0.04px;
    margin-left: 12px;
    justify-content: center;
    align-items: center;
}
.complaintReportingCard .btnItemThemeL{
    font-size: 18px !important;
}
.complaintReportingCard.redBtn{
    border: 1px solid #17B7BD;
    color: #17B7BD;
}

.complaintReportingCard .workStatus{
    position: absolute;
    top: 0;
    right: 0;
    width: 66px;
    height: 32px;
    line-height: 32px;
   
}
.complaintReportingCard .bottomSpace{
    width: 100%;
    height: 53px;
}
</style>